@media (min-width: 0) and (max-width: 400px) {
  .half {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .project__example {
    min-width: 200px;
    height: 150px;
  }

  .project__image {
    height: 150px;
  }

  .half {
    margin: 10px 0;
  }
}

@media (min-width: 401px) {
  .half {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 300px;
  }

  .project__example {
    min-width: 200px;
    min-height: 300px;
    max-height: 300px;
    margin: 0;
  }

  .project__image {
    height: 300px;
  }
}

@media (min-width: 0) and (max-width: 800px) {
  .project {
    flex-direction: column !important;
    padding: 0;
    width: 80vw;

    &__description {
      margin-bottom: 10px;
    }

    &__description,
    &__example {
      width: 100%;
    }
  }

  .half {
    height: 100%;
    margin: 20px 0;
  }

}

@media (min-width: 401px) and (max-width: 650px) {
  .half {
    margin: 0;
  }
}

@media (min-width: 801px) and (max-width: 999px) {
  .project {
    width: 80vw;
    padding-bottom: 20px;

    &__container {
      width: 100vw;
    }

    &__description {
      width: 40vw
    }

    &__example {
      width: 60vw;
    }
  }

  .row {
    margin-right: 40px;
  }

  .row-reverse {
    margin-left: 40px;
  }
}

@media (min-width: 1000px) {
  .project {
    // width: 70vw;
    margin: 40px auto;
    
    &__description {
      width: 40vw
    }

    &__example {
      width: 60vw;
    }
  }

  .row {
    margin-right: 40px;
  }

  .row-reverse {
    margin-left: 40px;
  }

}

.project {
  position: relative;
  flex-direction: column;
  z-index: 2;
  display: flex;
  margin: 0 auto 40px;
  box-sizing: border-box;
  max-height: 100%;
  justify-content: center;
  align-items: center;

  &__title {
    font-size: 1.8rem;
    margin: 10px;
  }

  &__text {
    font-size: 1rem;
    overflow: hidden;
  }

  &__description {
    box-sizing: border-box;
    flex-direction: column;
    padding: 0px 30px;
    text-align: left;
    flex-grow: 1;
    font-family: 'Noto Sans', sans-serif;
    min-width: 100px;
    border-radius: 10px;
    color: rgb(250, 250, 250);
    transition: transform 0.3s;

    &:hover {
      transform: scale(1.03);
    }
  }

  &__example {
    min-width: 200px;
  }

  &__image {
    min-height: 100%;
    width: 100%;
    transition: transform .5s, background-size 0.5s;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    border-radius: 10px;
    max-width: 560px;

    &:hover {
      transform: scale(1.05);
    }
  }

  &__buttons {
    display: flex;
    font-family: 'Urbanist', sans-serif;
    font-weight: 300;
    margin: 20px 0;
    font-size: 1rem;
  }

  &__title {
    font-family: 'Urbanist', sans-serif;
    font-weight: 400;
  }
}

.half {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 560px;
}

.button {
  width: 100px;
  padding: 5px;
  margin: 5px;
  text-align: center;
  transition: border 0.2s;
  position: relative;
  transform: perspective(1px) translateZ(0);
  transition: 0.4s;

  &:before {
    content: '';
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
    background: black;
    clip-path: inset(0 100% 0 0);
    opacity: 0.2;
    transition: 0.4s;
  }

  &:hover::before {
    clip-path: inset(0 0 0 0);
    height: 100%;
    width: 100%;
  }
}


a {
  text-decoration: none
}

.row {
  background-color: #005165;
  color: white;
}

.row-reverse {
  background-color: #ffc000;
  color: black;
}