@media (min-width: 0) and (max-width:800px) {
  #hidden {
    display: none;
  }
}

@media (min-width: 801px) {
  .hidden {
    display: none;
  }

  .nav-list {
    margin-right: 50px;
  }
}

.header {
  margin: 0 20px;
  min-height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;

  &-block {
    display: flex;
    align-items: center;
  }

  &-button__image {
    height: 30px;
    filter: invert(78%) sepia(15%) saturate(6618%) hue-rotate(358deg) brightness(106%) contrast(105%);
  }

  &__burger {
    &-list {
      right: 0;
      left: 40%;
      margin: 0;
      top: 0;
      position: fixed;
      padding: 0 0 0 6vw;
      padding-top: 30%;
      height: 100%;
      flex-direction: column;
      z-index: 9999;
      list-style: none;
      background-color: #ffc000;
    }

    &-item {
      padding-bottom: 20px;
      font-size: 5vh;
    }
  }

  &__link {
    margin-right: 1vw;
    // filter: invert(10%) sepia(98%) saturate(4124%) hue-rotate(180deg) brightness(100%) contrast(102%);
    filter: invert(78%) sepia(15%) saturate(6618%) hue-rotate(358deg) brightness(106%) contrast(105%);

    &:last-child {
      margin-right: 0;
    }
  }

  &__icon {
    height: 25px;
    width: 25px;
    border-radius: 100%;
  }
}

.logo {
  &-image {
    height: 40px;
  }
}

.nav {
  &-list {
    padding: 0;
    display: flex;
    list-style: none;
    color: #ffc000;
  }

  &-item {
    margin: 0 1vw;
    text-transform: uppercase;
    cursor: pointer;
    transition: all 0.3s linear;

    &:hover {
      // text-decoration: underline;
      font-style: italic;

    }
  }
}

.burger__image {
  cursor: pointer;
  transition: transform 0.3s;

  &:hover {
    transform: scale(1.2);
  }
}